form {
    width: 90%;
    max-width: 300px;
    background-color: #1f1e48;
    padding: 33px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 10px;
}
  
label {
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    color: white;
    margin-bottom: 26px;
}
  
textarea {
    background: #151430;
    border: none;
    border-radius: 10px;
    color: #97a5a5;
    font-size: 20px;
    text-align: center;
    padding: 12px;
    height: 96px;
    width: calc(100% - 25px);
}
  
textarea::placeholder {
    color: #A5A5A5;
    font-family: 'Montserrat';
    font-weight: 400;
}
  
textarea:focus {
    outline-color: #61DAFA;
}
  
.TodoForm-buttonContainer {
    margin-top: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
  
.TodoForm-button {
    cursor: pointer;
    display: inline-block;
    font-size: 20px;
    color: #202329;
    font-weight: 400;
    width: 120px;
    height: 48px;
    border-radius: 2px;
    border: none;
    font-family: 'Montserrat';
}
  
.TodoForm-button--add {
    background: #485097;;
    box-shadow: 0px 0px 5px #485097;
    color: white;
    border-radius: 10px;
}
  
.TodoForm-button--cancel {
    background: transparent;
    color: white;
}